import React from "react";
import Link from "gatsby-link";


export default ({ data }) => {
  const cjelineSet = new Set();
  return (
    <div className="container">
      <h2 className="title">Matematika {data.allMarkdownRemark.edges[0].node.frontmatter.razred}</h2>
        <div className="breadcrumbs"><span><Link to="/">Naslovna</Link> </span></div>
    
      <div className="list">

      {data.allMarkdownRemark.edges
        .filter((node,index) =>{
          if (!cjelineSet.has(node.node.frontmatter.cjelina) && node.node.frontmatter.cjelina) {
            cjelineSet.add(node.node.frontmatter.cjelina);
            return true;
          }
          else {            
            return false;
          }
        })
        .map(({ node }) =>          
          <Link to={`/${node.frontmatter.razred}/${node.frontmatter.cjelina.toLowerCase().replace(/ž/g, "z").replace(/š/g, "s").replace(".", "-").replace(/č/g, "c").replace(/ć/g, "c").replace(/đ/g, "d").replace(/(\s+|\. )/g, "-")}/`} className="item"  key={node.id}>       
              {node.frontmatter.cjelina}
          </Link>
      )}
      </div>
    </div>
  )
}
export const query = graphql`
query RazredIndexQuery($razred: Int) {
  allMarkdownRemark(sort: {order: ASC, fields: [frontmatter___brojCjeline]}, filter: {frontmatter: {razred: {eq: $razred} }}) {
    edges {
      node {
        id
        frontmatter {
          razred
          cjelina
          brojCjeline
        }
      }
    }
  }
}

`




